#tool-bar {
  width: 100vw;
  height: var(--bar-height);
  background-color: var(--bar-bg);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin: 0%;
}

#get-array-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-line-pack: end;
      align-content: flex-end;
}

#get-array {
  color: #fff !important;
  text-transform: uppercase;
  text-decoration: none;
  background: #60a3bc;
  padding: 10px;
  border-radius: 50px;
  display: inline-block;
  text-align: center;
  border: none;
  -webkit-transition: all 0.4s ease 0s;
  -o-transition: all 0.4s ease 0s;
  transition: all 0.4s ease 0s;
}

#get-array:hover {
  text-shadow: 0px 0px 6px rgba(255, 255, 255, 1);
  box-shadow: 0px;
  -webkit-box-shadow: 0px 5px 40px -10px rgba(0, 0, 0, 0.57);
  -moz-box-shadow: 0px 5px 40px -10px rgba(0, 0, 0, 0.57);
  -webkit-transition: all 0.4s ease 0s;
  -o-transition: all 0.4s ease 0s;
  transition: all 0.4s ease 0s;
  -ms-flex-item-align: center;
      align-self: center;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.sort-button {
  color: #494949 !important;
  text-transform: uppercase;
  text-decoration: none;
  background: #ffffff;
  padding: 6px;
  border: 4px solid #494949 !important;
  display: inline-block;
  -webkit-transition: all 0.4s ease 0s;
  -o-transition: all 0.4s ease 0s;
  transition: all 0.4s ease 0s;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.sort-button:hover {
  color: #ffffff !important;
  background: #f6b93b;
  border-color: #f6b93b !important;
  -webkit-transition: all 0.4s ease 0s;
  -o-transition: all 0.4s ease 0s;
  transition: all 0.4s ease 0s;
}

#size-range-container {
  margin-left: 3px;
  margin-right: 3px;
  border: 3px solid;
  border-radius: 5px;
  border-color: brown;
  background-color: brown;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-line-pack: end;
      align-content: flex-end;
  -ms-flex-align: center;
      align-items: center;
  text-align: center;
  width: 250px;
  font-size: 1rem;
}

#size-range-title {
  color: white;
  margin-left: auto;
  margin-right: auto;
}

#array-size {
  color: white;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  width: 50px;
}

#speed-range-container {
  margin-left: 0.3%;
  margin-right: 1%;
  border: 3px solid;
  border-radius: 5px;
  border-color: green;
  background-color: green;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-line-pack: end;
      align-content: flex-end;
  text-align: center;
  width: 250px;
  -ms-flex-align: center;
      align-items: center;
  font-size: 1rem;
}

#speed-range-title {
  color: white;
  margin-left: auto;
  margin-right: auto;
}

#change-speed {
  width: 30%;
}

#speed-label {
  color: white;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  width: 50px;
}

.range-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

ul {
  list-style: none;
}

@media screen and (max-width: 1000px) {
  .sorting-buttons,
  .range-container {
    display: none;
  }
}



