}

#statistics {
  background-color: #666699;
  height: 20px;
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 0%;
  -ms-flex-pack: center;
      justify-content: center;
}

#comparisons {
  margin-left: auto;
  margin-right: 20px;
  display: -ms-flexbox;
  display: flex;
}

#array-accesses {
  margin-left: 20px;
  margin-right: auto;
  display: -ms-flexbox;
  display: flex;
}



